@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.gora {
  background: #581845;
  height: 100px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.loginSection {
  background: #632ce4;
  position: absolute;
  top: 100px;
  padding: 30px;
  right: 0;
  z-index: 1000;
  opacity: 0;
}



.fas {
  color: #fff;
  font-size: 30px;
  margin-right: 30px;
  cursor: pointer;
  transition: .3s;
}

.fas:hover {
  color: #FFC300
}



.login{
  margin: 5px;
  border: none;
  color: #FFC300;
  display: block;
  font-size: 18px;
  border: 0.5px solid #FFC300;
  border-radius: 7%;
  cursor: pointer;
  transition: .3s;
}

.login:hover {
  color: #DAF7A6;
  background-color: #C70039;
}

.buttons{
  display: block;
  height: 100%;
  width: 100%;
  padding: 15px 30px;
  text-align: center;
  text-decoration: none;
  color: #FFC300;
}

.NavIcon {
  margin-left: 2rem;
  font-size: 2rem;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  transition: .3s;
}

.tytul {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 2.4rem;
  letter-spacing: 1px;
  color: #DAF7A6;
  margin-left: auto;
  margin-right: auto;
  text-decoration: none;
}

.t2 {
  text-decoration: none;
  color: #DAF7A6;
  transition: .3s;
}

.t2:hover {
  color: #C70039;
}

.wrap {
  width: 100%;
}

.mainNav {
  display: flex;
  color: #DAF7A6;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  list-style: none;
  height: 60px;
  text-decoration: none;
  font-size: 20px;
  transition: .3s;
}

.mainNav:hover {
  background: #FF5733;
  border-left: 4px solid #632ce4;
  cursor: pointer;
}

.dropdown {
  background: #9c4c6b;
  height: 60px;
  padding-left: 3rem;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #DAF7A6;
  font-size: 18px;
  transition: .3s;
}

.dropdown:hover {
  background: #C70039;
  cursor: pointer;
}



::-webkit-scrollbar {
    width: 20px;
  }
  
  ::-webkit-scrollbar-track {
    background-color: transparent;
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: #d6dee1;
    border-radius: 20px;
    border: 6px solid transparent;
    background-clip: content-box;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background-color: #a8bbbf;
  }
